import React from 'react';
import { Link } from "react-router-dom";

import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';


const OtherProject = (props) => {
    const styles = {
        main: {
            padding: "12px 12px",
            margin: 6,
            borderRadius: 10,
            height: 200,
            width: 320,
        },
        tags: {
            height: 36,
            backgroundColor: props.tagColor,
            borderRadius: 30,
            fontFamily: "Montserrat",
            fontWeight: 600,
            fontSize: "66%",
            color: props.tagTextColor,
            margin: "0.5rem 1rem 0 0"
        }
    }

    return (
        <Grid container>
            <Grid item direction="column" align="center" >
                <Link to={props.link}>
                    <Card elevation={0} style={styles.main}>
                        <CardContent elevation={0} align="left" style={{ backgroundColor: "inherit" }}>
                            <Grid container>
                                <Grid item xs={12} sx={{ display: 'contents' }} >
                                    <h1 class="small-card">{props.name}</h1>
                                </Grid>
                                <Grid item xs={12}>
                                    <p>{props.description}</p>
                                </Grid>
                                <Grid item xs={12}>
                                    {props.tags.map((tag) => (
                                        <Button variant="contained" disabled style={styles.tags}>
                                            {tag}
                                        </Button>
                                    ))}
                                </Grid>
                            </Grid>
                        </CardContent>
                    </Card>
                </Link>
            </Grid>
        </Grid>
    );
}

export default OtherProject;