import React from 'react';
import { Link } from "react-router-dom";

import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import CardActionArea from '@mui/material/CardActionArea';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import { makeStyles } from '@mui/styles';


import Boop from "../components/boop";

import AFCBanner from "../assets/thumbnails/afc.png";
import ZOTAPOTTYBanner from "../assets/thumbnails/zotapotty.png";
import UniqloBanner from "../assets/thumbnails/uniqlo.png";
import { Scale } from '@mui/icons-material';



const useStyles = makeStyles({
    card: {
      maxWidth: 345,
      top: '50%',
      left: '50%',
      borderRadius: 10,
      position: 'absolute',
      transform: 'translate(-50%, -50%)',
      backgroundSize: '200%',
      boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
      transition: '0.6s',
      backgroundImage: 'linear-gradient(45deg, #FFC312, #EE5A24, #00a8ff)',
      '&:hover': {
        backgroundPosition: 'right'
      }
    }
  });


const ProjectCard = () => {
    const classes = useStyles();

    // *edit project data here*
    const projectData = [
        {name: "Abound Food Care", description: "A volunteer management system", tags: ["UI/UX Design", "Usability Testing"], tagColor: "#E8F1F2", tagTextColor: "#73A1A9", image: AFCBanner, bgColor: "#B3D0D5", link: "/abound-food-care"},
        {name: "ZOTAPOTTY", description: "A UCI restroom finder and review app", tags: ["App Design", "User Research"], tagColor: "#FCF6E7", tagTextColor: "#DBA72A", image: ZOTAPOTTYBanner, bgColor: "#F7E3B0", link: "/zotapotty"},
        {name: "UX Case Study: Uniqlo", description: "A redesigned Uniqlo mobile app", tags: ["Redesign", "User Research"], tagColor: "#FCF3FA", tagTextColor: "#BC88AF", image: UniqloBanner, bgColor: "#DDC9D8", link: "/uniqlo"},
    ];

    
    return (
            <Grid container>
                {projectData.map((project) => (
                    <Grid item direction="column" xs={12} align="center" sx={{py: 8}}>
                        <Card elevation={0} style={{borderRadius: 15}}>
                        
                            <Link to={project.link} component={CardActionArea}>
                            <Grid>
                                <CardMedia
                                    component="img"
                                    image={project.image}
                                    style={{borderRadius: 15}}
                                />
                                </Grid>
                            </Link>
                            
                            <CardContent elevation={0} align="left" style={{backgroundColor: "inherit"}}>
                                <Grid container xs={12}>
                                    <Grid item sx={{ display: 'contents'}}>
                                        <h1 class="card">{project.name}</h1>
                                        {project.tags.map((tag) => (
                                            <Button variant="contained" disabled style={{height: 36, backgroundColor: project.tagColor, borderRadius: 30, fontFamily: "Montserrat", fontWeight: 600, color: project.tagTextColor, margin: "1.2rem 0 0 1rem"}}>
                                                {tag}
                                            </Button>
                                        ))}
                                    </Grid>
                                    <Grid item xs={12}>
                                        <h2 class="card">{project.description}</h2>
                                    </Grid>
                                </Grid>
                                </CardContent>
                            </Card>
                    </Grid>
                ))}
            </Grid>
    );
}

export default ProjectCard;

