import React, { Component } from 'react'
import { Link, Outlet } from "react-router-dom";

import Header from "../components/header";
import Footer from "../components/footer";

import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';

import Profile from '../assets/profile.jpg';



const styles = {
  // general container
  mainContainer: {
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center'
  },
  // blue highlighted background
  highlightContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: '#F0F3F9',
  },

  // SECTIONS WITHIN CONTAINERS
  // general formatting
  generalSection: {
    flex: 1,
    maxWidth: 852,
    width: '100%',
    margin: '24px 10px',
    // flexDirection: 'column',
    justifyContent: 'space-between'
  },
  // wider formatting
  largeSection: {
    flex: 1,
    maxWidth: 1052,
    width: '100%',
    margin: '24px 10px',
    // flexDirection: 'row',
    justifyContent: 'space-between'
  },
}


class AboutMe extends Component {
  render() {
    return (
      <div>
        <Header />
        <Grid container style={styles.mainContainer}>
        
        <div style={{ height: 90 }} />
          {/* <Grid item xs={12} style={styles.largeSection}>
            <h1 style={{ textAlign: "center" }}>About Me</h1>
          </Grid> */}

          <Grid container item xs={12} style={styles.largeSection}>
            <Grid item xs={5}>
              <img src={Profile} alt="Profile image" style={{borderRadius: 10}}/>
            </Grid>
            <Grid item xs={7} style={{ paddingLeft: 38, paddingTop: 18 }}>
              <p>Hello, my name is Christina Vu! I am currently attending UCI as an Informatics major
                specializing in Human-Computer Interaction. I am expected to graduate in Spring 2022 and 
                am currently looking for an internship opportunity to better hone by skills as a UI/UX designer!</p>

              <p>In my free time, I enjoy learning how to cook, trying new restaurants with friends, and 
                taking film photos. </p>

              <div style={{ height: 42 }} />

              <p> Education  University of California, Irvine (Expected Graduation: June 2022)</p>
              <p> Skills  User Research, Usability Testing, Personas, Storyboards, Sketching,
                Wireframing, Mockups, Prototyping, Visual Design, Typography, Competitive Analysis,
                Front-End Web Development</p>
              <p> Coding  HTML, CSS, JavaScript, Python, C++, Basic Java</p>
              <p> Tools  Figma, Adoble XD, Adobe Photoshop</p>
              <p> Currently Learning  Adobe Illustrator, Adobe Animate</p>

            </Grid>
          </Grid>
        </Grid>

        <div style={{ height: 80 }} />

        <Grid container style={styles.mainContainer}>
          <Grid item xs={12} style={styles.generalSection}>
            <Footer />
          </Grid>
        </Grid>
      </div>

    )
  }
}

export default AboutMe;