import React from 'react';
import Grid from '@mui/material/Grid';
import SvgIcon from '@mui/material/SvgIcon';

import {ReactComponent as Envelope} from '../assets/icons/envelope-regular.svg';
import {ReactComponent as LinkedIn} from '../assets/icons/linkedin-in-brands.svg';
import {ReactComponent as Medium} from '../assets/icons/medium-m-brands.svg';


const Footer = () => {
    return (
            <footer>
                    <Grid container style={{margin: "6rem 0"}}>
                        <Grid item xs={12}>
                            <h1>Thanks for visiting my portfolio!</h1>
                            <p>If you're interested or would like to chat, please email me at <a href="mailto:chrissyvu24@gmail.com" target="_blank" rel="noopener noreferrer">chrissyvu24@gmail.com</a>.
                            </p>
                        </Grid>
                        <Grid container item xs={12} id="social-icons">
                            <Grid item>                 
                                <a href="mailto:chrissyvu24@gmail.com" target="_blank" rel="noopener noreferrer">
                                    <SvgIcon component={Envelope} inheritViewBox/>
                                </a>
                            </Grid>
                            <Grid itemScope>                 
                                <a href="https://www.linkedin.com/in/christina-vu-493560164/" target="_blank" rel="noopener noreferrer">
                                    <SvgIcon component={LinkedIn} inheritViewBox/>
                                </a>
                            </Grid>
                            <Grid item>                 
                                <a href="https://medium.com/@vucd1/" target="_blank" rel="noopener noreferrer">
                                    <SvgIcon component={Medium} inheritViewBox/>
                                </a>
                            </Grid>
                        </Grid>
                    </Grid>
            </footer>
    )
};
export default Footer;