import React, {Component} from 'react'
import Home from "./pages/homepage";
import { HashRouter, Route, Switch } from "react-router-dom";
import About from "./pages/about-me";
import Zotapotty from "./pages/zotapotty";
import Uniqlo from "./pages/uniqlo";
import AboundFoodCare from "./pages/afc";
import NotFound from "./pages/not-found";
import ScrollToTop from './components/scrollToTop';


class App extends Component {
  render() {
    return (
    <div>
      <HashRouter basename={process.env.PUBLIC_URL}>
        <ScrollToTop/>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route path="/about" component={About} />
          <Route path="/uniqlo" component={Uniqlo} />
          <Route path="/zotapotty" component={Zotapotty}/>
          <Route path="/abound-food-care" component={AboundFoodCare}/>
          <Route component={NotFound}/>
        </Switch>
      </HashRouter>
    </div>
    )
  }
}


export default App


