import React from "react";
import Grid from "@mui/material/Grid";
import Container from '@mui/material/Container';

import BannerImage from "../assets/introv1.jpg";
import Persona from "../assets/persona.png";

const styles = {
    heroContainer: {
        // height: '40vw',
        // height: 750,
        height: '89.5vh',
        width: `100vw`,
        backgroundImage: `url(${BannerImage})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        margin: '0 -8px',
    }
};

const Intro = () => {
    return (
        <Grid
            container
            alignItems="center"
            style={styles.heroContainer} >
            <Container maxWidth="xl">
                <Grid container item alignItems="center" sx={{ justifyContent: "center", display: { xs: 'none', md: 'flex' } }}>
                    <Grid item xs={5}>
                        <h1 class="intro">Hi there! I'm <strong>Christina</strong>.</h1>
                        <h2 class="intro">I am a <strong>UI/UX designer</strong> focused on designing with empathy and creating memorable experiences.</h2>
                    </Grid>
                    <Grid item xs={3}>
                        <img class="med-img" src={Persona} alt="Drawing of Christina" />
                    </Grid>
                </Grid>
                <Grid container item direction="column" alignItems="center" sx={{ justifyContent: "center", display: { xs: 'flex', md: 'none' } }}>
                    <Grid item align="center">
                        <img class="small-img" src={Persona} alt="Drawing of Christina" />
                    </Grid>
                    <Grid item>
                        <h1 class="intro">Hi there! I'm <strong>Christina</strong>.</h1>
                        <h2 class="intro">I am a <strong>UI/UX designer</strong> focused on designing with empathy and creating memorable experiences.</h2>
                    </Grid>

                </Grid>
            </Container>
        </Grid>
    )
};
export default Intro;